.clikable {
  opacity: 0;
}

.checkmark {
  position: relative;
  height: 17px;
  width: 17px;
  border-style: solid;
  border-width: 1px;
  border-color: #dde9ed;
  opacity: 1;
  background-color: #fff;
  border-radius: 50%;
}

.clikable:checked ~ .checkmark {
  background-color: #bd1010;
  opacity: 1;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.clikable:checked ~ .checkmark:after {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.container_input_div .checkmark:after {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #fff;
}

